import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import InfoRow from '@vkontakte/vkui/dist/components/InfoRow/InfoRow';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import FormLayoutGroup from "@vkontakte/vkui/dist/components/FormLayoutGroup/FormLayoutGroup";
import Input from "@vkontakte/vkui/dist/components/Input/Input";
import FormLayout from "@vkontakte/vkui/dist/components/FormLayout/FormLayout";
import PanelSpinner from "@vkontakte/vkui/dist/components/PanelSpinner/PanelSpinner";

import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import {animateScroll as scroll} from 'react-scroll'
import './Persik.css';
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import Div from "@vkontakte/vkui/dist/components/Div/Div";


import axios from "axios";

const osName = platform();

class Chat extends React.Component {
	constructor(props) {
		super(props);
		// создадим реф в поле `textInput` для хранения DOM-элемента

		this.textInput = React.createRef();
		this.pollBack = true;

		this.state = {
			msgList: [],
			err: null
		};

		//const [msgList, setMsgList] = useState([]);

	}

	componentDidMount() {
		this.focusTextInput();
		this.refreshMsgs();
	}

	componentDidUpdate() {
		//console.log(this.state.msgList);
		scroll.scrollToBottom();
	}

	componentWillUnmount() {
		this.pollBack = false;
	}

	processMsgs(msgArr) {
		this.setState({msgList: [...this.state.msgList, ...msgArr]});
	}

	async refreshMsgs() {
		console.log('Trying to fetch history with TOKEN[' + this.props.fetchedUser.data.token + ']');
		while (this.pollBack) {
			await axios.post("https://das.ict.nsc.ru:12000/updates", {
				'token': this.props.fetchedUser.data.token
			})
				.then((res) => {
					console.log(res);
					if (res.data.status == 'ok'){
						if (res.data.total > 0) {
							this.processMsgs(res.data.response);
						}
						/*
						status: "ok"
response: [{message: "tester returned to chat!", uname: "[SERVER]", id: 17}]
total: 1
*/
					} else {
						console.log(res.data.desc);
						//setActivePanel('login');
					}
				});
		}
	}
	sendMsg() {
		console.log(this.textInput.current.value);
		console.log('Sending message with TOKEN[' + this.props.fetchedUser.data.token + ']');
		if ( this.textInput.current.value.length === 0 ) {return false;}

		axios.post("https://das.ict.nsc.ru:12000/send", {
			'token': this.props.fetchedUser.data.token,
			'message': this.textInput.current.value
		})
			.then((res) => {
				console.log(res);
				if (res.data.status == 'ok'){
					// OK!
				} else {
					console.log(res.data.desc);
					switch (res.data.code) {
						case '402':
							this.setState({err: 'Вы уже зарегистрированы'});
							break;
						case '501':
							this.setState({err: 'Никнейм должен быть среди [A-Za-z0-9\\_\\-\\.]'});
							break;
						default:
							this.setState({err: 'Неизвестная ошибка'});
							break;
					}
					/*
					"402", "desc": "no valid token presented"}
					"501", "desc": "forbidden, because you don\'t provided [message, token]"

					*/
				}
			});


		// Clear input
		this.textInput.current.value = '';
	};

	focusTextInput() {
		// Установим фокус на текстовое поле с помощью чистого DOM API
		// Примечание: обращаемся к "current", чтобы получить DOM-узел
		this.textInput.current.focus();
	}

	render() {


		return (
			<Panel id={this.props.id}>
				<PanelHeader
				left={
				<HeaderButton onClick={this.props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
				</HeaderButton>}
				>
					Чат
				</PanelHeader>

				{/*History*/}



				<Group className='historyGroup'>
					<Div>
						{(this.state.msgList == 0) ? <PanelSpinner /> :
							this.state.msgList.map(value =>
						{
							//console.log(value);
							return (
								<InfoRow key={value.id} className='msgBox' id={`msg-id${value.id}`} title={`${value.uname}`}>
									{value.message}
								</InfoRow>
							);
						})}


					</Div>
				</Group>




				{/*Footer*/}

				<Group className='inputRow'>
					<Div style={{display: 'flex'}} className='minimalDiv'>
						<Input placeholder="Сообщение"
							   className='msgInput'
							   getRef={this.textInput}
							   onKeyDown={(value) => {if (value.key == "Enter") {this.sendMsg();}}}
							   status={(this.state.err == null) ? 'valid' : 'error'}
							   bottom={(this.state.err == null)? null : this.state.err}
						/>

						<Button
							size="xl"
							level="1"
							className='msgBtn'
							onClick={() => {this.sendMsg()}}>
							Отпр.
						</Button>
					</Div>
				</Group>



			</Panel>
		);
	}
}
/*
			<div>
				<input
					type="text"
					ref={this.textInput} />

				<input
					type="button"
					value="Фокус на текстовом поле"
					onClick={this.focusTextInput}
				/>
			</div>
 */

export default Chat;
