import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ModalRoot from '@vkontakte/vkui/dist/components/ModalRoot/ModalRoot';
import ModalPage from '@vkontakte/vkui/dist/components/ModalPage/ModalPage';
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout';
import FormLayoutGroup from '@vkontakte/vkui/dist/components/FormLayoutGroup/FormLayoutGroup';
import Input from '@vkontakte/vkui/dist/components/Input/Input';
import ModalPageHeader from '@vkontakte/vkui/dist/components/ModalPageHeader/ModalPageHeader';
import Separator from '@vkontakte/vkui/dist/components/Separator/Separator';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import axios from 'axios';

import Home from './panels/Home';
import Chat from './panels/Persik';
import Login from './panels/Login';
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import Div from "@vkontakte/vkui/dist/components/Div/Div";

let mutex = {id: 2,
first_name: "Филипп Джанковски",
last_name: "",
timezone: 7}

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [activeModalS, setActiveModal] = useState(null);
	const [fetchedUser, setUser] = useState(mutex);
	const [activeChat, selectChat] = useState(null);
	const [loginErrMsg, setLEM] = useState(null);

	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	/* DATA STORE */
	//const [msgList, setMsgList] = useState([]);


	console.log(fetchedUser);

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_dark';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});

		async function fetchData() {
			let user = null;
			console.log(window.location.href);
			if (window.location.href[4] == ':') {
				user = fetchedUser;
				console.log('Local version');
			} else {
				console.log('Global version');
				user = await connect.sendPromise('VKWebAppGetUserInfo');
			}

			setUser(user);

			let dataf = await axios.get("https://das.ict.nsc.ru:12000/reg/" + user.id + ':0:secret').then((res) => {
				if (res.data.status == 'ok'){
					user.data = res.data.response;
				} else {
					setActivePanel('login');
				}
			});
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	//var logData = {name: null, pass: null};
	var logDataName = React.createRef();
	var logDataPass = React.createRef();

	async function tryLogin() {
		let user = fetchedUser;
		setPopout(<ScreenSpinner size='large' />);
		if (logDataName.current.value.length < 1 || logDataPass.current.value.length < 1) {
			setLEM('Введите все данные');
			setPopout(null);
			return false;
		}
		let dataf = await axios.get("https://das.ict.nsc.ru:12000/reg/" + user.id + ':' + logDataName.current.value + ':' + logDataPass.current.value).then((res) => {
			if (res.data.status == 'ok'){
				user.data = res.data.response;
				setActivePanel('home');
			} else {
				console.log('Auth Error: ' + res.data.desc);
				/*
				"401", "desc": "already registered, use correct data"
				"306", "desc": "uname is required to be defined only in [A-Za-z0-9\_\-\.]"
				"302", "desc": "uname or key isn\'t correct key = (len > 4), uname = (len > 4) "
				 */
				switch (res.data.code) {
					case '401':
						setLEM('Вы уже зарегистрированы');
						break;
					case '306':
						setLEM('Никнейм должен быть среди [A-Za-z0-9\\_\\-\\.]');
						break;
					case '302':
						setLEM('Длина никнейма и пароля должна быть более 4');
						break;
					default:
						setLEM('Неизвестная ошибка');
						break;
				}
				//setLEM(res.data.desc);
				//setActivePanel('home');
			}
		});
		setUser(user);
		setPopout(null);
	}


	const modal = (
		<ModalRoot activeModal={activeModalS}>
			<ModalPage
				id={'register'}

				header={
					<ModalPageHeader>
						Регистрация
					</ModalPageHeader>
				}
			>
				<FormLayout>

				</FormLayout>
			</ModalPage>
		</ModalRoot>
	);

	return (
		<View activePanel={activePanel} popout={popout} modal={modal}>
			<Home id='home' fetchedUser={fetchedUser} go={go} setActiveModal={setActiveModal} />
			<Chat id='chat' go={go} activeChat={activeChat} fetchedUser={fetchedUser} />
			<Login id='login' logDataName={logDataName} logDataPass={logDataPass} tryLogin={tryLogin} errMsg={loginErrMsg}/>
		</View>
	);

}

export default App;
