const Home = ({ id, go, fetchedUser, setActiveModal }) => (
	<Panel id={id}>
		<PanelHeader className="Panel">Bubbly</PanelHeader>
		{fetchedUser &&
		<Group title="Вы вошли как">
			<Cell

				description={fetchedUser.data ? `${fetchedUser.first_name} ${fetchedUser.last_name}` : "Мы загружаем ваши данные..."}
			>
				{`${fetchedUser.data ? fetchedUser.data.uname : "Загрузка..."}`}
			</Cell>

		</Group>}

		<Group title="Чаты">
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="chat">
					<Cell
						description="Нажмите, чтобы присоединиться"
					>
						{`${fetchedUser && fetchedUser.data ? fetchedUser.data.motd  : "Загрузка..."}`}
					</Cell>
				</Button>
			</Div>
		</Group>
		<Group title="Версия приложения">
			<Div>
				0.1.7.0221 (Build Date: {preval`module.exports = new Date().toLocaleString();`}.)
			</Div>
		</Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
