import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import './Home.css';
import FormLayoutGroup from "@vkontakte/vkui/dist/components/FormLayoutGroup/FormLayoutGroup";
import Input from "@vkontakte/vkui/dist/components/Input/Input";
import FormLayout from "@vkontakte/vkui/dist/components/FormLayout/FormLayout";


const Login = ({ id, logDataName, logDataPass, tryLogin, errMsg}) => (
	<Panel id={id}>
		<PanelHeader className="Panel">Регистрация</PanelHeader>

		<FormLayout>

			<FormLayoutGroup top="Введите данные">
                <Input placeholder="Ваш псевдоним" id='name' getRef={logDataName}
				/>
                <Input placeholder="Ваш пароль" id='pass' getRef={logDataPass}
				/>
			</FormLayoutGroup>

			<Button size="xl" level="2" onClick={() => {console.log(logDataName.current.value);console.log(logDataPass.current.value); tryLogin();}}>
				Войти
			</Button>
            <Div>
                <b>{errMsg && errMsg}</b>
            </Div>

		</FormLayout>

	</Panel>
);


export default Login;
